<template>
  <BaseLayout>
    <Breadcrumbs />
    <div class="container">
      <div class="row">
        <ProductFilter class="col-lg-3" :dontShowActiveFilters="true" />
        <BrandPageContent class="col-lg-9" />
      </div>
    </div>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { useI18n } from "vue-i18n";
import BaseLayout from "@/base/core/components/common/BaseLayout.vue";
import Breadcrumbs from "@/base/core/components/Breadcrumbs.vue";
import ProductFilter from "@/base/core/components/ProductFilter.vue";
import BrandPageContent from "@/base/core/components/BrandPageContent.vue";

const store = useStore();
const route = useRoute();
const { t } = useI18n();

const currentbrand = computed(() =>
  store.getters["product/getBrandSliderByCode"](route.params.code)
);

onMounted(() => {
  const bcrumb = {
    current: currentbrand.value?.title,
    routes: [
      {
        name: t("brands"),
        route_link: "/merken",
      },
    ],
  };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
